import React, { Component } from 'react'
import Helmet from 'react-helmet'
import { PageWrapper, PostContentWrapper } from '../components/content-wrapper'
import NavBar from '../components/NavBar'
import Footer from '../components/Footer'
import PageTitle from '../components/PageTitle'
import PostRankTable, { PostRankData } from '../components/PostRankTable'
import axios from 'axios'
import searchService from '../service/SearchService'
import moment from 'moment'
import styled from 'styled-components'
import { TabWrapper, Tab } from '../components/PostRankTab'
import LoadingSpinner from '../components/LoadingSpinner'
import * as R from 'ramda'

const TableWrap = styled.div`
  min-height: 70vh;
`

type PageView = {
  count: number,
  page: string,
}

type Props = {
  data: any,
}

type State = {
  postList: PostRankData[],
  pageViews: PageView[],
}

const WEEK = 'week'
const MONTH = 'month'
const YEAR = 'year'

class StatsRoute extends Component<Props, State> {
  tabs = [WEEK, MONTH, YEAR]
  tabNameMap = {
    [WEEK]: '지난 7일',
    [MONTH]: '지난 30일',
    [YEAR]: '지난 1년',
  }

  routeTitleMap = {}

  defaultTab = this.tabs[0]

  constructor(props) {
    super(props)
    this.state = {
      currentTab: this.defaultTab,
      pageViews: [],
      postList: [],
      isLoadingData: true,
    }
  }

  componentDidMount() {
    searchService.addMessageListener({
      onReceiveRouteTitle: ({ routeTitleMap }) => {
        this.routeTitleMap = R.clone(routeTitleMap)
        this.handleChangeTab(this.state.currentTab)
      },
    })

    // search-worker 가 데이터를 모두 불러올때까지 대기.
    // 완료되면 라우트 타이틀 맵을 가져온 후 포스트 페이지뷰 불러옴
    var readyForWorker = setInterval(() => {
      if (searchService.isReady) {
        console.log('ready')

        searchService.getRouteTitleMap()
        clearInterval(readyForWorker)
      }
    }, 200)
  }

  handleChangeTab = (tab = this.defaultTab) => {
    let startDate
    const yesterday = moment().subtract(1, 'day')
    const DATE_FORMAT = 'YYYY-MM-DD'

    this.setState({
      currentTab: tab,
      isLoadingData: true,
    })

    switch (tab) {
      case WEEK:
        startDate = yesterday.subtract(1, 'week').format(DATE_FORMAT)
        break
      case MONTH:
        startDate = yesterday.subtract(1, 'month').format(DATE_FORMAT)
        break
      case YEAR:
        startDate = yesterday.subtract(1, 'year').format(DATE_FORMAT)
        break

      default:
        break
    }

    axios
      .get(`https://blogapi.rhostem.ml/api/ga/post_pageviews`, {
        params: {
          startDate,
          endDate: 'today',
        },
      })
      .then(res => {
        const pageViews: PageView[] = res.data

        this.setState({
          isLoadingData: false,
          pageViews,
          postList: pageViews
            .map((pageView: PageView) => {
              if (this.routeTitleMap[pageView.page]) {
                return {
                  title: this.routeTitleMap[pageView.page],
                  route: pageView.page,
                  pageView: pageView.count,
                }
              } else {
                console.error('route not found for', pageView.page)
                return {}
              }
            })
            .filter(post => !!post.title),
        })
      })
  }

  render() {
    const siteMetadata = this.props.data.site.siteMetadata
    const title = `인기 포스트`
    const pageTitle = `${title} · ${siteMetadata.title}`

    return (
      <PageWrapper>
        <Helmet
          title={pageTitle}
          description="Google analytics 데이터를 사용해 구성한 인기 페이지"
          link={[
            {
              rel: 'canonical',
              href: `${siteMetadata.url}${this.props.location.pathname}`,
            },
          ]}
          meta={[
            {
              property: 'og:url',
              content: `${siteMetadata.url}${this.props.location.pathname}`,
            },
            { property: 'og:title', content: pageTitle },
          ]}
        />
        <NavBar />
        <PostContentWrapper>
          <PageTitle>{title}</PageTitle>

          <TabWrapper>
            {this.tabs.map((tab, i) => (
              <Tab
                key={tab}
                onClick={() => this.handleChangeTab(tab)}
                isSelected={tab === this.state.currentTab}
              >
                {this.tabNameMap[tab]}
              </Tab>
            ))}
          </TabWrapper>

          <TableWrap>
            {this.state.isLoadingData && <LoadingSpinner />}
            <PostRankTable postList={this.state.postList} />
          </TableWrap>
        </PostContentWrapper>
        <Footer />
      </PageWrapper>
    )
  }
}

export default StatsRoute

export const statsQuery = graphql`
  query statsQuery {
    site {
      siteMetadata {
        title
        url
      }
    }
  }
`



